<template>
    <transition name="fade">
        <div class="home">
            <!---->
            <div class="home_navigation">
                <!---->
                <div style="position: absolute;width:100%;height:100%;overflow: hidden;">
                    <img src="../assets/background_cloud.gif"  :style="navigationBGImageStyle">
                </div>
                <div style="background-color: rgba(70, 70, 70, 0.6);width:100%;height:100%;position: absolute;"></div>
                <!---->
                <div style="position: absolute;width:30px;height:30px;">
                    <img src="../assets/left_top_triangle.png" @click="backToLaunch" style="width:100%;height:100%;cursor: pointer;">
                </div>
                <!---->
                <div style="padding-top:50px">
                    <div class="avatar_border">
                        <div class="avatar_view">
                            <img src="../assets/avatar.jpeg">
                        </div>
                    </div>
                    <div style="text-align:center;align-items:center;position: relative;">
                        <p style="color:white;font-size: 18px;"></p>
                    </div>
                    <div style="width:200px;height:1px;background-color: white;left:50%;margin-left:-100px;position: relative;"></div>
                    <div class="my_info_view">
                        <ul>
                            <li>
                                <a href="https://github.com/ShmilyLin" target="_blank" v-on:click.stop><img src="../assets/github.png"></a>
                            </li>
                            <li>
                                <a href="http://www.jianshu.com/u/4a549ca9b701" target="_blank" v-on:click.stop><img src="../assets/jianshu.png"></a>
                            </li>
                            <li>
                                <a href="Mailto:522402543@qq.com" target="_blank" v-on:click.stop><img src="../assets/email.png"></a>
                            </li>
                        </ul>
                    </div>
                    <div style="width:150px;height:1px;background-color: white;left:50%;margin-left:-75px;position: relative;margin-top:15px;"></div>
                </div>
                <!---->
                <div class="catalog_div">
                    <ul>
                        <li>
                            <p @click="myResumeClicked">  </p>
                        </li>
                    </ul>
                </div>
                <div class="catalog_div" :style="navigationCatalogStyle" style="padding-top:30px">
                    <div style="width:265px;height:100%;overflow:scroll;">
                        <ul v-if="!showArticle">
                            <li v-for="albumItem in this.$store.state.listData.blog">
                                <p @click="selectedAlbum(albumItem)">{{albumItem.albumName}}</p>
                            </li>
                        </ul>
                        <ul v-if="showArticle">
                            <li v-for="articleItem in this.currentAlbum.articles">
                                <p @click="selectedArticle(articleItem)">{{articleItem.articleName}}</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <!---->
            <div class="router_view">
                <router-view></router-view>
                <div class="bottom_illustrate_view">
                    <div style="width:200px;height:1px;background-color: rgb(150, 150, 150);position: relative;"></div>
                    <p> <router-link to="/resume"></router-link>  <a href="https://cn.vuejs.org/" target="_blank">Vue</a>  <a href="" target="_blank">GitHub</a>  -  2017</p>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'home',
    data () {
        return {
            navigationBGImageStyle: {
                'height': window.innerHeight + 'px',
                'width': ((window.innerHeight*575)/400) + 'px',
                'margin-left': '-' + ((((window.innerHeight*575)/400) - 250)/2) + 'px'
            },
            navigationCatalogStyle: {
                'height': (window.innerHeight - 350) + 'px'
            },

            showAlbumArticlesList: false, // 
            showArticle: false, // 
        }
    },
    methods: {
        myResumeClicked: function () {
            this.$router.push('resume');
        },
        // Launch
        backToLaunch: function() {
            this.$router.replace('/');
        },
        // 
        selectedAlbum: function (album) {
            this.$router.push('/home/ablum/' + album.albumIndex);
            this.showAlbumArticlesList = true;
            this.$store.commit('setCurrentAlbum', album);
        },
        // 
        selectedArticle: function (article) {
            this.showArticle = true;
            this.$store.commit('setCurrentArticle', article);
        }
    }
}
</script>

<!-- // scopedstyleCSS -->
<style scoped>
/*css*/
/*@import './component/css/easydialog.min.css';*/
.home {
    position: absolute;
    width: 100%;
    height: 100%;
}
.home_navigation {
    width: 250px;
    height: 100%;
    background-color: rgb(55, 55, 55);
    /*background: rgb(55, 55, 55) url(../assets/background_cloud.gif) no-repeat center;
    background-size: 1150px 800px;*/
    float: left;
    position: relative;
    overflow: hidden;
}
.blur {	
	filter:blur(10px);
	-webkit-filter:blur(10px);
	-moz-filter:blur(10px);
	-ms-filter:blur(10px);
	-o-filter:blur(10px);
}
/**/
.avatar_border {
	position: relative;
	width:66px;
	height:66px;
	border-radius:33px;
	border:1px solid white;
	background-color:white;
	left: 50%;
	margin-left: -33px;
	
}
.avatar_view {
	width:60px;
	height:60px;
	border-radius:30px;
	border:1px solid white;
	overflow:hidden;
	margin-left:2px;
	margin-top:2px;
}
.avatar_view img {
	width:100%;
	height:100%;
}

.my_info_view {
    text-align:center;
    align-items:center;
	width:150px;
	position: relative;
	left: 50%;
	margin-left: -75px;
	overflow: hidden;
	margin-top: 20px;
}

.my_info_view ul {
    height: 100%;
}
.my_info_view li {
	float: left;
	width: 33%;
	height: 20px;
	overflow: hidden;
	list-style-type:none;
}

.my_info_view li img {
	width:20px;
	height:20px;
}


/**/
.catalog_div {
    width: 100%;
    color: white;
    text-align: center;
    align-items: center;
    overflow: hidden;
}

.catalog_div li {
    position: relative;
    width: 150px;
    left: 50%;
    margin-left: -75px;
	height: 30px;
    overflow: hidden;
	list-style-type:none;
    border-bottom: 1px solid white;
}
.catalog_div p {
    padding: 0;
    padding-top: 7px;
    margin: 0;
    cursor: pointer;
}

/**/

.router_view {
    height: 100%;
    float: left;
    max-width: 750px;
    padding-left: 50px;
}

/**/

.bottom_illustrate_view {
    font-size:12px;
    color:rgb(150, 150, 150);
    padding-bottom: 20px;
}

/*.bottom_illustrate_view p {

}*/

.bottom_illustrate_view a:link {
    color:rgb(55, 55, 55);
}

.bottom_illustrate_view a:visited {
    color:rgb(150, 150, 150);
}
.bottom_illustrate_view a:hover {
    color:rgb(55, 55, 55);
}
.bottom_illustrate_view a:active {
    color:rgb(55, 55, 55);
}
</style>


